body {
  margin: 0;
  padding: 0;
  width: 100vw;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.gt{
  font-family: GT Alpina !important;
}
.calibre{
  font-family: Calibre, sans-serif;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.bg-fixed{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, #bdc6f5, #f4b896);
  background-attachment: fixed;
  background-size: cover;
}
.bg{
  background-image: url('./images/bg.svg');
  background-repeat: no-repeat;
  background-size: cover;
}
.zin{
  z-index: 55;
}
.trans{
  transform: translateX(-300px)
}

.custom-shadow{
  box-shadow: -5px 5px 0 0 rgba(0, 0, 0, .1);
}
.ant-input,.ant-select .ant-select-selector{
 border: 0 !important;
 outline: none !important;
 height: 40px !important;
}
.ant-input::placeholder,.ant-select-selection-search-input::placeholder,.ant-select-selection-placeholder{
  color: #000 !important;
  font-family: Calibre !important;
  font-size: 16px !important;
}
.ant-select-selection-placeholder{
  transform: translateY(5px);
}
.ant-select{
  width: 100% !important;
}
.ant-select-selection-item{
  height: 100%;
  display: flex;
  align-items: center;
}
@keyframes  revolve {
  100%{
    transform: rotate(360deg);
  }
}
.loader{
  width: 20px;
  height: 20px;
  border: 3px solid #230b59;
  border-radius: 50%;
  display: inline-flex;
  border-bottom-color: transparent;
  animation-name: revolve;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  margin-right: 5px;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.btn{
  @apply bg-primary text-[#ffffff] px-6 py-2 rounded-md font-[14px];
}

.label-text{
  @apply text-secondary text-2xl font-semibold;
}

.footer{
  background-color: rgb(24 23 43);
}


